import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { DocumentComponent } from './document.component';
import { DocumentSetupComponent } from './components/document-setup/document-setup.component';
import { DocumentSetupCategoryComponent } from './components/document-setup/document-setup-category/document-setup-category.component';
import { FolderListsComponent } from './components/folder-lists/folder-lists.component';
import { UserListsComponent } from './components/user-lists/user-lists.component';
import { DocumentCommonDownloadComponent } from '../components/shared/document-common-download/document-common-download.component';
import { DocumentsReportsComponent } from './components/documents-reports/documents-reports.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: DocumentSetupComponent },
	{ path: 'common', component: FolderListsComponent },
	{ path: 'user', component: UserListsComponent },
	{ path: 'user/:id', component: FolderListsComponent},
	{ path: 'reports', component: DocumentsReportsComponent },					
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DocumentsRoutingModule { }
