import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { DocumentService } from '../../../../api/services/document.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-user-lists',
  templateUrl: './user-lists.component.html',
  styleUrls: ['./user-lists.component.scss']
})
export class UserListsComponent implements OnInit {

  
  isUserDataLoaded:boolean = false

  totalUserDocument:any
  itemUserLimit:any = 5;
  ItemUserStartIndex:any = 0;
  ItemUserEndIndex:any;

  isMobile:boolean = false;

  getAllDocumentData:any
  unitFieldType:string = "firstName";
  unitOrder:boolean = true;

  activePgStart:any
  activePgEnd:any

  constructor(
    private documentService:DocumentService,
    private cookieService:CookieService
  ) { }

  getUserIndexParams(event){    
    this.ItemUserStartIndex = event.ItemStartIndex;
    this.ItemUserEndIndex = event.ItemEndIndex;
    this.itemUserLimit = event.itemLimit;
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }
  
  sortUnitData(type){    
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  ngOnInit() {

    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

    this.documentService.GetAllDocCountByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => { 

      this.isUserDataLoaded = true         
      this.getAllDocumentData = res

      this.totalUserDocument = this.getAllDocumentData.length

      if(this.totalUserDocument>this.itemUserLimit){
        this.ItemUserEndIndex = this.itemUserLimit;
      }
      else {
        this.ItemUserEndIndex = this.totalUserDocument;
      }
      
    },
    error => {}
    );

  }
}
