import { Component, OnInit,ViewChild, TemplateRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { DocumentService } from '../../../../api/services/document.service';
import { UserService } from '../../../../api/services/user.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import * as _ from 'underscore';

import { LookupService } from '../../../../api/services/lookup.service';

@Component({
  selector: 'app-folder-lists',
  templateUrl: './folder-lists.component.html',
  styleUrls: ['./folder-lists.component.scss']
})

export class FolderListsComponent implements OnInit {

  isCommmonDataLoaded:boolean = false;

  folderList:any;
  folderByCategory:any;
  accessibleList:any

  confirmDialogRef:any;

  listOfFiles: any[] = [];
  fileList: File[] = [];
  isFileAdded:boolean = false;

  selectedFile: File;
  selectedTab = -1;

  uploadDescription:any;
  uploadcategoryId:any;
  uploadroleID:any;

  uploadFileItem:any;

  fileToUpload:any;

  isUserPage:boolean = false;
  unitNo:any;
  getalldocument:any;

  @ViewChild("uploadTemplate", { static: false }) uploadTemplate: TemplateRef<any>;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private sharedService:SharedService,
    private lookupService: LookupService,
    private UserService:UserService,
    private documentService:DocumentService,
    private cookieService:CookieService,
    public dialog: MatDialog,
  ) {}
 
  uploadFile(files: FileList){

    this.isFileAdded = true;   
    this.fileToUpload = files.item(0); 
    var selectedFile = files[0];
    //let formData = new FormData(); 
    //formData.append('file', this.fileToUpload, this.fileToUpload.name); 
    this.fileList.push(selectedFile);
    this.listOfFiles.push(this.fileToUpload)

  }

  bytesToSize(bytes) {

    if(bytes < 1024) return bytes + " Bytes";
     else if(bytes < 1048576) return(bytes / 1024).toFixed(3) + " KB";
     else if(bytes < 1073741824) return(bytes / 1048576).toFixed(3) + " MB";
     else return(bytes / 1073741824).toFixed(3) + " GB";

   }

   deleteFile(index){
     
    // Delete the item from fileNames list
    this.listOfFiles.splice(index, 1);
    // delete file from FileList
    this.fileList.splice(index, 1);

    this.selectedFile = null;

    if(this.fileList.length == 0 && this.listOfFiles.length == 0){
      this.isFileAdded = false;
    }

  }
	
  openUploadModal(){

    this.confirmDialogRef = this.dialog.open(this.uploadTemplate, {
      panelClass: 'material',
      disableClose: true,
      data: alert
    });

    this.confirmDialogRef.afterClosed().subscribe(
      data => { }         
    );

  }

  submitUploadAdmindoc(){
    
    let uploadDetails = {
      "ApartmentId" : parseInt(this.cookieService.get('apartmentId')),
      "ApartmentBlockUnitId" : this.isUserPage ? this.getUserDocumentData(this.unitNo).apartmentBlockUnitID : null,
      "UserDocumentCategoryId" : this.uploadcategoryId,
      "UserDocumentTypeId" : 1,
      "UserDocumentAccessibleToId" :this.uploadroleID,
      "UserId" : this.isUserPage ? this.getUserDocumentData(this.unitNo).userID : null,
      "UserDocumentName" : this.uploadDescription,
      "Description" : "test document",
      "IsActive":true,
      "InsertedBy":1,
      "IsPublic" : true,
      "files" : this.listOfFiles[0]
    }
    
    this.selectedTab = -1;
    this.confirmDialogRef.close(); 
    this.sharedService.setAlertMessage("Document uploaded successfully"); 

    this.documentService.addUserDocument(uploadDetails).subscribe((res:any) => {       
      this.getCategoryData(this.uploadcategoryId)
    },
    error => {
      console.log(error)
    });    
    
  }

  getCategoryData(id) {

    var data = _.filter(this.folderByCategory, function( item ) {
      if(item.lookupValueId == id) {
        item.documentCount++
      }
    });

  }

  isMobileView() {
    return window.innerWidth <= 991;
  }
  
  isTabSelected(){
  	return this.selectedTab != -1 ? 'show' : '';
  }

  showFolderDetails(id) {
    
    if(!this.isMobileView()){
      if(this.selectedTab != id)
        this.selectedTab = id;
      else
        this.selectedTab = -1;
    }
    else {     
     this.router.navigate(['ams/document-common-download/'+id]);
    }
  }

  setFileCount(){
    
    let folderList = this.folderList
    _.each(this.folderByCategory, function(id) {
      id.documentCount = 0;
      var data = _.filter(folderList, function( item ) {        
        if(id.lookupValueId == item.categoryId) {
          id.documentCount= item.documentCount         
        }        
      });
    })
    
  }

  getUserDocumentData(unitNo){
    var data = _.filter(this.getalldocument, function( item ) {        
      if(item.apartmentBlockUnitNumber === unitNo) {
        return item;       
      }        
    });
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0]
    }

  }

  ngOnInit() {

    if(this.route.params['value'].id != undefined){
       this.isUserPage = true;   
       this.unitNo = this.route.params['value'].id    
    }

    let adminCategory = {
      "ApartmentId" : parseInt(this.cookieService.get('apartmentId')),
      "IsPublic" : true
    }

    this.UserService.getAllRoles(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {          
      this.accessibleList = res
    },
    error => {}
    );

    this.lookupService.getLookupValueByLookupTypeId(62).subscribe((res:any) => { 

      this.folderByCategory = res

      this.documentService.GetAllDocCountByCategory(adminCategory).subscribe((res:any) => { 

        this.folderList = res         
        this.setFileCount()        
        
        if(this.isUserPage) {

          this.documentService.GetAllDocCountByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
              
            this.isCommmonDataLoaded = true
            this.getalldocument = res
              
          },
          error => {}
          );
        }else{
          this.isCommmonDataLoaded = true
        }

      },
      error => {}
      );
      
    },
    error => {}
    );

  }

}
