import { Component, OnInit, Input, OnChanges, SimpleChanges, Injector, HostListener, ViewChild, TemplateRef} from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

import { LookupService } from '../../../../api/services/lookup.service';
import { DocumentService } from '../../../../api/services/document.service';
import { FileDetailsService } from '../../../../api/services/file-details.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-document-common-download',
  templateUrl: './document-common-download.component.html',
  styleUrls: ['./document-common-download.component.scss']
})
export class DocumentCommonDownloadComponent implements OnInit {

  @Input() selectedTab:number;

  isDocumentDataLoaded:boolean = false;
  isNoDownloads:boolean = false;
  isSeperatePage:boolean = false;

  listofFiles:any;
  fileUrl:any;

  userPage:boolean = false;
  userUnitNo:any;

  getalldocument:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private lookupService: LookupService,
    private documentService:DocumentService,
    private fileDetailsService:FileDetailsService,
    private cookieService:CookieService,
    private sanitizer: DomSanitizer
  ) { }

  isTabSelected(){
  	return this.selectedTab != -1 ? 'show' : '';
  }

  isMobileView() {
    return window.innerWidth <= 991;
  }

  isNotSlide() {
    return this.isSeperatePage ? 'full' : '';
  }

  downloadFile(file){
    
    let obj = {     
      "savefileNameWithPath" : file.userDocumentName,
      "fileNameOnBlob" : file.filePath
    }
   
    this.fileDetailsService.DownloadAsync(obj).subscribe((res:any) => {      
    },
    error => {}
    );   

  }

  getUserDocumentData(unitNo){
    var data = _.filter(this.getalldocument, function( item ) {        
      if(item.apartmentBlockUnitNumber === unitNo) {
        return item;       
      }        
    });
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0]
    }

  }

  ngOnInit() {
    if(this.route.params['value'].id != undefined && this.isMobileView()){
      this.isSeperatePage = true;      
    }

    if(this.route.params['value'].id != undefined){
      this.userPage = true;      
      this.userUnitNo = this.route.params['value'].id 
    }

    var unitInfo = this.userUnitNo;
    
    //if(this.userPage) {     
      this.documentService.GetAllDocCountByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
        
        this.getalldocument = res   
       
        if(this.getUserDocumentData(unitInfo).apartmentBlockUnitID != undefined) {
          this.documentService.GetAllDocByApartmentBlockUnitId(this.getUserDocumentData(this.userUnitNo).apartmentBlockUnitID).subscribe((res:any) => {
        
            this.listofFiles = res
            this.isDocumentDataLoaded = true          
            this.isNoDownloads = this.listofFiles.length      
          },
          error => {}
          );

        } else {

          let documentByCategory = {
            "apartmentId" : parseInt(this.cookieService.get('apartmentId')),
            "docCategoryId" : this.route.params['value'].id
          }
          this.isDocumentDataLoaded = false
          this.documentService.getUserDocumentsByUserDocumentCategoryId(documentByCategory).subscribe((res:any) => {
            
            this.listofFiles = res
            this.isDocumentDataLoaded = true
          },
          error => {}
          );

        }
      },
      error => {}
      );

    //}
  }

  ngOnChanges(changes: SimpleChanges) {
   
    this.isDocumentDataLoaded = false    
   
    if(this.userPage) {   
        
      this.documentService.GetAllDocByApartmentBlockUnitId(this.getUserDocumentData(this.userUnitNo).apartmentBlockUnitID).subscribe((res:any) => {
        this.isDocumentDataLoaded = true   
        this.listofFiles = res             
        this.isNoDownloads = this.listofFiles.length  

      },
      error => {}
      );

    } else {

      let documentByCategory = {
        "apartmentId" : parseInt(this.cookieService.get('apartmentId')),
        "docCategoryId" : this.selectedTab
      }

      this.documentService.getUserDocumentsByUserDocumentCategoryId(documentByCategory).subscribe((res:any) => {
          this.isDocumentDataLoaded = true    
          this.listofFiles = res          
          //this.isNoDownloads = true
          this.isNoDownloads = this.listofFiles.length      
      },
      error => {}
      );
    }
  }

}
